/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.raw-material-details {
  padding: 0 0 2em;
  position: relative;
}
@media only screen and (min-width: 992px) {
  .raw-material-details {
    display: flex;
  }
}
.raw-material-details .raw-material-details-image {
  width: 100%;
}
@media only screen and (min-width: 992px) {
  .raw-material-details .raw-material-details-image {
    padding: 0 1em 0 0;
    width: 60%;
    margin-left: calc(150px + 2em);
  }
}
.raw-material-details .raw-material-details-image img {
  width: 100%;
  height: auto;
}
.raw-material-details .raw-material-details-description {
  padding: 2em;
  width: 100%;
  background: #003877;
  color: white;
  z-index: 2;
}
@media only screen and (min-width: 992px) {
  .raw-material-details .raw-material-details-description {
    position: relative;
    right: 0;
    text-align: left;
    display: flex;
    flex-direction: column;
    width: 45%;
    margin: 6em 0 0 -15em;
  }
}
.raw-material-details .raw-material-details-description .ui.header {
  font-size: 2em;
  color: white;
}
.raw-material-details .raw-material-details-description button {
  margin: 2em 0 0;
}
@media only screen and (min-width: 992px) {
  .raw-material-details .raw-material-details-description button {
    margin-bottom: 0;
  }
}
