/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.App {
  text-align: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.App .ui.grid .row .left {
  padding-left: 3em;
  padding-right: 3em;
  display: flex;
  justify-content: center;
}
.App .ui.grid .row .left .App-materials {
  max-width: 1020px;
  width: 100%;
  position: relative;
}
.App .ui.grid .row .left .App-materials-header {
  text-align: center;
  width: 100%;
}
.App .ui.grid .row .left .App-materials-header-type {
  text-align: center;
}
@media only screen and (min-width: 768px) {
  .App .ui.grid .row .left .App-materials-header-type {
    width: calc(100% - 150px);
  }
}
@media only screen and (min-width: 768px) {
  .App .ui.grid .row .left .App-materials {
    display: flex;
    flex-wrap: wrap;
  }
}
.App .ui.grid .row .left .App-materials .raw-material-selector {
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .App .ui.grid .row .left .App-materials .raw-material-selector:nth-child(2) {
    max-width: 150px;
  }
}
.App .ui.grid .row .left .App-materials .raw-material-selector:nth-child(2) .ui.menu {
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .App .ui.grid .row .left .App-materials .progress-bars {
    width: calc(100% - 150px);
    padding: 2em 0 2em 2em;
  }
}
@media only screen and (min-width: 768px) {
  .App .ui.grid .row .left .App-materials-frontpage-img {
    margin-top: 1em;
  }
}
