/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
.navbar {
  height: 70px;
  z-index: 2;
  position: relative;
  width: 100vw;
}
.navbar .ui.menu {
  height: 100%;
  background: #003877;
  width: 100%;
  justify-content: space-between;
}
.navbar .ui.menu .item:first-child {
  padding-left: 0;
}
.navbar .ui.menu .item:before {
  background: none;
}
.navbar .ui.menu .item > img:not(.ui) {
  width: 8em;
}
.navbar .ui.menu .navbar-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  padding-top: 1.8em;
}
.navbar .ui.menu .navbar-title.item:before {
  content: none;
}
.navbar .ui.menu .navbar-title .ui.header {
  color: white;
}
