/*******************************
     User Global Variables
*******************************/
/* colors (from LIFE Designguide) */
/* fonts */
@media only screen and (min-width: 768px) {
  .chat {
    position: fixed;
    top: 0;
    right: 0;
    padding-right: 0;
    height: 100vh;
    z-index: 1;
    width: 25%;
  }
}
.chat .segment {
  height: 100%;
}
.chat .chat-wrapper.ui.segment {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 0;
}
.chat .chat-messages {
  padding: 1em 1em;
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 768px) {
  .chat .chat-messages {
    margin-top: 70px;
  }
}
.chat .chat-messages .chat-messages-user,
.chat .chat-messages .chat-messages-message {
  margin-right: 4em;
  display: flex;
  margin-bottom: 1em;
  float: left;
  margin-left: 0.5em;
}
.chat .chat-messages .chat-messages-user i,
.chat .chat-messages .chat-messages-message i {
  margin-top: 0.8em;
  margin-right: 0.8em;
}
.chat .chat-messages .chat-messages-user p,
.chat .chat-messages .chat-messages-message p {
  border: 1px solid lightgray;
  border-radius: 20px;
  padding: 0.8em 1em;
  text-align: left;
}
.chat .chat-messages .chat-messages-user {
  margin-right: 0;
  margin-left: auto;
}
.chat .chat-messages .chat-messages-user p {
  background: #003877;
  color: white;
}
.chat .chat-messages .chat-messages-message-user-icon {
  border-radius: 100%;
  background: #DCDCDC;
  height: 2em;
  width: 2em;
  min-width: 2em;
  min-height: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1em;
  position: relative;
}
.chat .chat-messages .chat-messages-message-user-icon p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0;
  border: none;
  font-size: 0.9em;
  line-height: 0.9em;
}
.chat form.ui.form {
  padding: 1em 1em;
  display: flex;
  align-items: flex-end;
  border-top: 1px solid lightgray;
}
@media only screen and (min-width: 768px) {
  .chat form.ui.form {
    position: fixed;
    bottom: 0;
    background: white;
    width: 25%;
  }
}
.chat form.ui.form textarea {
  margin-right: 1em;
  resize: none;
}
.chat form.ui.form button.pulsate {
  position: relative;
}
.chat form.ui.form button.pulsate:before {
  content: '';
  position: absolute;
  display: block;
  width: 300%;
  height: 300%;
  box-sizing: border-box;
  top: -100%;
  left: -100%;
  border-radius: 100%;
  background-color: #003877;
  animation: pulse-ring 1.5s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}
@keyframes pulse-ring {
  0% {
    transform: scale(0.33);
  }
  80%,
  100% {
    opacity: 0;
  }
}
@keyframes pulse-dot {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}
